import  * as ordersActions from './order.action';

//featuresKey

export  const OrderFearturesKey = 'orders';

//initial state
let initialState = {
  loading : false,
  errorMessage:'',
  cartItems:[],
  placedOrder : {},
    allOrders : []
};

export  const reducer = (state = initialState , action)=>{

    let {type , payload} =action;
    switch (type){
        case ordersActions.ADD_TO_CART:
            let exist = state.cartItems.find(item => item._id === payload.item._id );
            if(exist) return state;
            return{
                ...state,
                cartItems :[...state.cartItems , payload.item]
            };
        case ordersActions.ADD_TO_CART_FAILURE:
            return{
                ...state,
                errorMessage:payload
            };

            //increase card Item qty
        case ordersActions.INCR_CART_ITEM:

            let updatedCartItems = state.cartItems.map(cartItem =>{
               if (cartItem._id === payload.productId){
                   return{
                       ...cartItem,
                       qty : cartItem.qty +1
                   }
               }
               return cartItem
            });
            return{
                ...state,
                cartItems:[...updatedCartItems]
            };

        case ordersActions.INCR_CART_ITEM_FAILURE:
            return{
                ...state,
                errorMessage:payload
            };

        //decrease card Item qty
        case ordersActions.DECR_CART_ITEM:

            let updateCartItems = state.cartItems.map(cartItem =>{
                if (cartItem._id === payload.productId){
                    return{
                        ...cartItem,
                        qty : (cartItem.qty > 1) ? cartItem.qty - 1 : 1
                    }
                }
                return cartItem
            });
            return{
                ...state,
                cartItems:[...updateCartItems]
            };

        case ordersActions.DECR_CART_ITEM_FAILURE:
            return{
                ...state,
                errorMessage:payload
            };

        //delete card Item
        case ordersActions.DELETE_CART_ITEM:
            let updatedCartItem = state.cartItems.filter(item =>{
                return item._id !== payload.productId
            });
            return{
                ...state,
                cartItems:[...updatedCartItem]
            };

        case ordersActions.DELETE_CART_ITEM_FAILURE:
            return{
                ...state,
                errorMessage:payload
            };
        // Make Stripe Payments
        case ordersActions.STRIPE_PAYMENT_REQUEST :
            return  {
                ...state,
                loading: true
            };
        case ordersActions.STRIPE_PAYMENT_SUCCESS :
            return  {
                ...state,
                loading: false
            };
        case ordersActions.STRIPE_PAYMENT_FAILURE :
            return  {
                ...state,
                loading: false,
                errorMessage: payload
            };
        // Make Stripe Payments
        case ordersActions.PLACE_ORDER_REQUEST :
            return  {
                ...state,
                loading: true
            };
        case ordersActions.PLACE_ORDER_SUCCESS :
            return  {
                ...state,
                loading: false,
                placedOrder : payload.order
            };
        case ordersActions.PLACE_ORDER_FAILURE :
            return  {
                ...state,
                loading: false,
                errorMessage: payload
            };

            //clear cart Item
        case ordersActions.CLEAR_CART_ITEMS :
            return  {
                ...state,
                cartItems:[]
            };
        case ordersActions.CLEAR_CART_ITEMS_FAILURE :
            return  {
                ...state,
                loading: false,
                errorMessage: payload
            };

            //get all orders
        // Make Stripe Payments
        case ordersActions.GET_ALL_ORDERS_REQUEST :
            return  {
                ...state,
                loading: true
            };
        case ordersActions.GET_ALL_ORDERS_SUCCESS :
            return  {
                ...state,
                loading: false,
                allOrders : payload.orders
            };
        case ordersActions.GET_ALL_ORDERS_FAILURE :
            return  {
                ...state,
                loading: false,
                errorMessage: payload
            };

        default:return state;
    }
};

