import React,{useState} from 'react';
import {useDispatch} from 'react-redux';
import {useHistory} from 'react-router-dom';
import brand from '../../assets/img/brand.PNG';
import {Link} from 'react-router-dom';
import * as userAction from "../../redux/users/user.action";
import * as alertActions from '../../redux/alert/alert.action';

let  UserLogin = ()=>{

    let dispatch = useDispatch();
    let history = useHistory();
    let [user , setUser] = useState({
        email:'',
        password :'',
    });

    let [userError , setUserError] = useState({
        emailError:'',
        passwordError :'',
    });

    let validateEmail = (event)=>{
        setUser({...user , email:event.target.value});
        let regExp = /^\w+([\\.-]?\w+)*@\w+([\\.-]?\w+)*(\.\w{2,3})+$/;
        !regExp.test(event.target.value) ?
            setUserError({...userError , emailError:'Enter proper email'}) :
            setUserError({ ...userError ,emailError :'' })
    };
    let validatePassword = (event)=>{
        setUser({...user , password:event.target.value});
        let regExp = /^[A-Za-z]\w{7,14}$/;
        !regExp.test(event.target.value) ?
            setUserError({...userError , passwordError:'Enter Proper Password'}) :
            setUserError({...userError , passwordError:''})
    };


    let submitUserLogin = (e)=>{
        e.preventDefault();
        let { email , password }=user;
        if (email!==''&& password!==''){
            dispatch(userAction.userLogin(user,history))

        }
        else{
            dispatch(alertActions.setAlert('Please fill the fields.' , 'danger'))
        }

    };
    return(
      <React.Fragment>
         {/* <pre>{JSON.stringify(user)}</pre>
          <pre>{JSON.stringify(userError)}</pre>*/}
          <section className="bg-brown text-white p-2">
              <div className="container">
                  <div className="row">
                      <div className="col">
                          <h3><i className="fa fa-user-cog"/>Welcome Login </h3>
                      </div>
                  </div>
              </div>
          </section>
          <section>
              <div className="container mt-3">
                  <div className="row ">
                      <div className="col-md-4 m-auto">
                          <div className="card ">
                              <div className="card-header bg-dark_ text-brown">
                                  <p className="h3">Login Here</p>
                              </div>
                              <div className="card-body">
                                  <form onSubmit={submitUserLogin}>
                                      <div className="form-group">
                                          <input
                                              name="email"
                                              value={user.email}
                                              onChange={validateEmail}
                                              type="email" className={`form-control ${userError.emailError.length>0 ? 'is-invalid' : ''}`} placeholder="Email"/>
                                      </div>
                                      <div className="form-group">
                                          <input
                                              name="email"
                                              value={user.password}
                                              onChange={validatePassword}
                                              type="password" className={`form-control ${userError.emailError.length>0 ? 'is-invalid' : ''}`} placeholder="Password"/>
                                      </div>
                                      <div>
                                          <input type="submit" className="btn btn-dark_ text-brown btn-sm" value="Submit"/>
                                      </div>
                                      <small>Have not a Account ? <Link to="/users/register">Register Please</Link></small>
                                  </form>
                              </div>
                              <div className="card-footer text-center bg-dark_">
                                  <img src={brand} alt="" width="150" height="35" />
                              </div>
                          </div>
                      </div>
                  </div>
              </div>
          </section>
      </React.Fragment>
  )
};
export  default UserLogin;