import React,{useState} from 'react';
import {useDispatch} from 'react-redux';
import {useHistory} from 'react-router-dom';
import brand from '../../assets/img/brand.PNG';
import {Link} from 'react-router-dom';
import * as userAction from '../../redux/users/user.action';
import * as alertAction from '../../redux/alert/alert.action';


let  UserRegister = ()=>{

    let dispatch = useDispatch();
    let history = useHistory();
    let [user , setUser] = useState({
        name :'',
        email:'',
        password :'',
        confirmPassword:''
    });

    let [userError , setUserError] = useState({
        nameError :'',
        emailError:'',
        passwordError :'',
        confirmPasswordError:''
    });
    let validateUsername = (event) => {
        setUser({...user , name : event.target.value});
        let regExp = /^[a-zA-Z0-9_ ]{4,16}$/;
        !regExp.test(event.target.value) ?
            setUserError({...userError , nameError: 'Enter a proper Username'})
            : setUserError({...userError , nameError: ''});
    };

    let validateEmail = (event)=>{
        setUser({...user , email:event.target.value});
        let regExp = /^\w+([\\.-]?\w+)*@\w+([\\.-]?\w+)*(\.\w{2,3})+$/;
        !regExp.test(event.target.value) ?
            setUserError({...userError , emailError:'Enter proper email'}) :
            setUserError({ ...userError ,emailError :'' })
    };
    let validatePassword = (event)=>{
        setUser({...user , password:event.target.value});
        let regExp = /^[A-Za-z]\w{7,14}$/;
        !regExp.test(event.target.value) ?
            setUserError({...userError , passwordError:'Enter Proper Password'}) :
            setUserError({...userError , passwordError:''})
    };

    let validateConfirmPassword = (event) => {
        setUser({...user , confirmPassword : event.target.value});
        (user.password !== event.target.value) ?
            setUserError({...userError , confirmPasswordError: 'Passwords Not Matched'})
            : setUserError({...userError , confirmPasswordError: ''});
    };




    //submitUserRegister
    let submitUserRegister =(e)=>{
        e.preventDefault();
        let {name , email , password , confirmPassword}=user;
        if (name !=='' && email !=='' && password !=='' && confirmPassword !==''){
            dispatch(userAction.userRegister(user , history));

        }
        else {
            dispatch(alertAction.setAlert('Please fill the fields', 'danger'));
            //dispatch(setAlert('Please Fill in the Fields' , 'danger'));
        }
    };

  return(
      <React.Fragment>
         {/* <pre>{JSON.stringify(user)}</pre>
          <pre>{JSON.stringify(userError)}</pre>*/}
          <section className="bg-brown text-white p-2">
              <div className="container">
                  <div className="row">
                      <div className="col">
                          <h3><i className="fa fa-user-cog"/>Register a User</h3>
                      </div>
                  </div>
              </div>
          </section>
          <section>
              <div className="container mt-3">
                  <div className="row ">
                      <div className="col-md-4 m-auto">
                          <div className="card ">
                              <div className="card-header bg-dark_ text-brown">
                                  <p className="h3">Register</p>
                              </div>
                              <div className="card-body">
                                    <form onSubmit={submitUserRegister}>
                                        <div className="form-group">
                                            <input
                                                name="name"
                                                value={user.name}
                                                onChange={validateUsername}
                                                type="text" className={`form-control ${userError.nameError.length>0 ? 'is-invalid' : ''}`} placeholder="Name"/>
                                        </div>
                                        <div className="form-group">
                                            <input
                                                name="email"
                                                value={user.email}
                                                onChange={validateEmail}
                                                type="email" className={`form-control ${userError.emailError.length>0 ? 'is-invalid' : ''}`} placeholder="Email"/>
                                        </div>
                                        <div className="form-group">
                                            <input
                                                name="password"
                                                value={user.password}
                                                onChange={validatePassword}
                                                type="password" className={`form-control ${userError.passwordError.length>0 ? 'is-invalid' : ''}`}  placeholder="Password"/>
                                        </div>
                                        <div className="form-group">
                                            <input
                                                name="confirmPassword"
                                                value={user.confirmPassword}
                                                onChange={validateConfirmPassword}
                                                type="password" className={`form-control ${userError.confirmPasswordError.length>0 ? 'is-invalid' : ''}`}  placeholder="Confirm Password"/>
                                        </div>
                                        <div>
                                            <input type="submit" className="btn btn-dark_ text-brown btn-sm" value="Submit"/>
                                        </div>
                                        <small>Have you already Registered ? <Link to="/users/login">Login Please</Link></small>

                                    </form>
                              </div>
                              <div className="card-footer text-center bg-dark_">
                                  <img src={brand} alt="" width="150" height="35" />
                              </div>
                          </div>
                      </div>
                  </div>
              </div>
          </section>
      </React.Fragment>
  )
};
export  default UserRegister;