import React,{useState} from 'react';
import {useDispatch , useSelector} from 'react-redux';
import {useHistory} from 'react-router-dom';
import * as productActions from '../../redux/products/product.action';
import * as userReducer from '../../redux/users/user.reducer';
import Spinner from '../../layout/util/Spinner/Spinner';


let  UploadProduct = ()=>{

    let dispatch = useDispatch();
    let history = useHistory();

    let userInfo = useSelector((state)=>{
        return state[userReducer.userFeatureKey]
    });

    let {user , loading} = userInfo;


    let [product  , setProduct]=useState({
        name:'',
        brand:'',
        category:'',
        image:'',
        price:'',
        qty:'',
        description:'',
        usage:''
    });

    let updateInput = (event)=>{
        setProduct({
            ...product,
            [event.target.name] : event.target.value
        });
    };

    // updateImage function
    let  updateImage = async (event) => {
        let imageFile = event.target.files[0];
        let base64Image = await convertBase64String(imageFile);
        setProduct({
            ...product,
            image : base64Image
        });
    };

    //convert image to base64 format
    let convertBase64String = (imageFile)=>{
        return new Promise((resolve , reject)=>{
            let fileReader = new FileReader();
            fileReader.readAsDataURL(imageFile);
            fileReader.addEventListener('load', ()=>{
                if(fileReader.result){
                    resolve(fileReader.result);
                }
                else {
                    reject('Error Occurred');
                }
            })
        });
    };
    let  submitProduct =(e)=>{
        e.preventDefault();
        dispatch(productActions.uploadProduct(product , history));

    };


  return(
      <React.Fragment>
          <section className="bg-brown text-white p-2">
              <div className="container">
                  <div className="row">
                      <div className="col">
                          <h3>Upload Products</h3>
                      </div>
                  </div>
              </div>
          </section>
          {
              Object.keys(user).length > 0 && user.isAdmin ?
                  <React.Fragment>
                      <section>
                          <div className="container mt-3">
                              <div className="row">
                                  <p className="h3">Upload Products Here</p>
                                  <p className="lead">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Assumenda fuga ipsam, ipsum nihil repellat sequi tempora vero! Atque corporis doloremque eius fuga, fugiat iste molestiae molestias, odit officiis quas voluptatem.</p>
                              </div>
                              <div className="row">
                                  <div className="col-md-6">
                                      <div className="card">
                                          <div className="card-body">
                                              <form onSubmit={submitProduct}>
                                                  <div className="form-group">
                                                      <input
                                                          name="name"
                                                          value={product.name}
                                                          onChange={updateInput}
                                                          type="text" className="form-control" required placeholder="Name" />
                                                  </div>
                                                  <div className="form-group">
                                                      <input
                                                          name="brand"
                                                          value={product.brand}
                                                          onChange={updateInput}
                                                          type="text" className="form-control"  required placeholder="Brand" />
                                                  </div>
                                                  <div className="form-group">
                                                      <select
                                                          name="category"
                                                          value={product.category}
                                                          onChange={updateInput}
                                                          className="form-control" required>
                                                          <option value="">Select a Category</option>
                                                          <option value="MEN">Men's Wear</option>
                                                          <option value="WOMEN">Women's Wear</option>
                                                          <option value="KIDS">Kid's Wear</option>
                                                      </select>
                                                  </div>
                                                  <div className="form-group">
                                                      <div className="custom-file">
                                                          <input
                                                              onChange={updateImage}
                                                              type="file" className="custom-file-input"
                                                              id="customFile"/>
                                                          <label className="custom-file-label" htmlFor="customFile">
                                                              {
                                                                  product.image !== '' ?
                                                                      <img src={product.image} alt=""
                                                                           width="30" height="30"/> :
                                                                      <span>Product Image</span>
                                                              }
                                                          </label>
                                                      </div>
                                                  </div>
                                                  <div className="form-group">
                                                      <input
                                                          name="price"
                                                          value={product.price}
                                                          onChange={updateInput}
                                                          type="number" required className="form-control" placeholder="Price" />
                                                  </div>
                                                  <div className="form-group">
                                                      <input
                                                          name="qty"
                                                          value={product.qty}
                                                          onChange={updateInput}
                                                          type="number"  required className="form-control" placeholder="Quantity" />
                                                  </div>
                                                  <div className="form-group">
                                          <textarea
                                              name="description"
                                              value={product.description}
                                              onChange={updateInput}
                                              className="form-control" rows="4" required placeholder="Descriptions"/>
                                                  </div>
                                                  <div className="form-group">
                                          <textarea
                                              name="usage"
                                              value={product.usage}
                                              onChange={updateInput}
                                              className="form-control" rows="4" required placeholder="Usages"/>
                                                  </div>
                                                  <div>
                                                      <input type="submit" className="btn btn-brown" value="Submit"/>
                                                  </div>

                                              </form>
                                          </div>
                                      </div>
                                  </div>
                              </div>
                          </div>
                      </section>
                  </React.Fragment>

                  :

                  <React.Fragment>
                      <section>
                          <div className="container p-3">
                              <div className="row">
                                  <div className="col">
                                      <p className="h3 text-danger">You are not Authorised to upload a product</p>
                                      <small>If you are an Admin, please contact your DBA to grant you the Admin access!</small>
                                  </div>
                              </div>
                          </div>
                      </section>
                  </React.Fragment>
          }

      </React.Fragment>
  )
};
export  default UploadProduct;