import React,{useEffect , useState} from 'react';
import {useDispatch ,useSelector} from 'react-redux';
import * as userActions from '../../redux/users/user.action';
import * as userReducer from '../../redux/users/user.reducer';
import Spinner from '../../layout/util/Spinner/Spinner';



let  UserProfile = ()=>{

    let dispatch = useDispatch();
    let userInfo = useSelector((state)=>{
        return state[userReducer.userFeatureKey]
    });
    let {user , loading} = userInfo;

    let[enableAddress , setEnableAddress] = useState(false);
    let [address , setAddress]= useState({
        flat:'',
        street:'',
        landmark:'',
        city:'',
        state:'',
        country:'',
        pin:'',
        mobile:''
    });


    useEffect(()=>{
        setAddress({
            flat:user && user.address ? user.address.flat : '',
            street:user && user.address ? user.address.street : '',
            landmark:user && user.address ? user.address.landmark : '',
            city:user && user.address ? user.address.city : '',
            state:user && user.address ? user.address.state : '',
            country:user && user.address ? user.address.country : '',
            pin:user && user.address ? user.address.pin : '',
            mobile:user && user.address ? user.address.mobile : ''
        });
    }, [user]);

    let updateInputAddress = (e)=>{
        setAddress({
            ...address,
            [e.target.name] : e.target.value
        });
    };

    let submitInputAddress=(e)=>{
        e.preventDefault();
        dispatch(userActions.updateAddress(address));
        /*setEnableAddress(false);*/


    };


  return(
      <React.Fragment>
          <section className="bg-brown text-white p-2">
              <div className="container">
                  <div className="row">
                      <div className="col">
                          <h3><i className="fa fa-user-circle"/>Your Profile </h3>
                      </div>
                  </div>
              </div>
          </section>


          {
              loading ? <Spinner/> :

                  <React.Fragment>
                      {
                            Object.keys(user).length >0 &&
                          <section>
                              <div className="container">
                                  <div className="row">
                                      <div className="col-md-3 mt-3">
                                          <img src={user.avatar} alt="" className="rounded-circle img-thumbnail img-fluid" width="200" height="200"/>
                                      </div>
                                      <div className="col-md-9 mt-3">
                                          <div className="card">
                                              <div className="card-header bg-dark_">
                                                  <h4 className="text-brown">Your Information</h4>
                                              </div>
                                              <div className="card-body">
                                                  <ul className="list-group">
                                                      <li className="list-group-item">
                                                          Name:<small className="lead font-weight-bold ml-2">{user.name}</small>
                                                      </li>
                                                      <li className="list-group-item">
                                                          Email:<small className="lead font-weight-bold ml-2">{user.email}</small>
                                                      </li>
                                                      {
                                                          user.address &&
                                                          <li className="list-group-item">
                                                              Mobile:<small className="lead font-weight-bold ml-2">{user.address.mobile}</small>
                                                          </li>
                                                      }
                                                  </ul>
                                              </div>
                                          </div>

                                          <div className="card mt-3">
                                              <div className="card-header bg-dark_">
                                                  <span className="text-brown">Billing Address</span>

                                                  <div className="custom-control custom-switch float-right">
                                                      <input
                                                          onChange={e => setEnableAddress(e.target.checked)}
                                                          type="checkbox" className="custom-control-input" id="customSwitch1"/>
                                                      <label className="custom-control-label text-brown" htmlFor="customSwitch1">Active Address</label>
                                                  </div>
                                              </div>
                                              <div className="card-body">
                                                  {
                                                      user.address && !enableAddress &&
                                                      <ul className="list-group">
                                                          <li className="list-group-item">
                                                              Mobile No:<small className=" lead ml-2 font-weight-bold">{user.address.mobile}</small>
                                                          </li>
                                                          <li className="list-group-item">
                                                              Home No:<small className="ml-2 lead">{user.address.flat}</small>
                                                          </li>
                                                          <li className="list-group-item">
                                                              Street:<small className=" lead  ml-2">{user.address.street}</small>
                                                          </li>
                                                          <li className="list-group-item">
                                                              Landmark:<small className="ml-2 lead ">{user.address.landmark}</small>
                                                          </li>
                                                          <li className="list-group-item">
                                                              City:<small className="ml-2 lead ">{user.address.city}</small>
                                                          </li>
                                                          <li className="list-group-item">
                                                              State:<small className="ml-2 lead ">{user.address.state}</small>
                                                          </li>
                                                          <li className="list-group-item">
                                                              Country:<small className="ml-2 lead ">{user.address.country}</small>
                                                          </li>
                                                          <li className="list-group-item">
                                                              Pin code:<small className="ml-2 lead ">{user.address.pin}</small>
                                                          </li>
                                                      </ul>
                                                  }
                                              </div>
                                              {
                                                  user.address && enableAddress &&
                                                      <div className="card-body">
                                                          <div>
                                                              <form onSubmit={submitInputAddress}>
                                                                  <div className="input-group mb-3">
                                                                      <span className="input-group-text bg-dark_ text-white">Flat:</span>
                                                                      <input
                                                                          name="flat"
                                                                          value={address.flat}
                                                                          onChange={updateInputAddress}
                                                                          type="text" className="form-control"/>
                                                                  </div>
                                                                  <div className="input-group mb-3">
                                                                      <span className="input-group-text bg-dark_ text-white">Street:</span>
                                                                      <input
                                                                          name="street"
                                                                          value={address.street}
                                                                          onChange={updateInputAddress}
                                                                          type="text" className="form-control"/>
                                                                  </div>
                                                                  <div className="input-group mb-3">
                                                                      <span className="input-group-text bg-dark_ text-white">Landmark:</span>
                                                                      <input
                                                                          name="landmark"
                                                                          value={address.landmark}
                                                                          onChange={updateInputAddress}
                                                                          type="text" className="form-control"/>
                                                                  </div>
                                                                  <div className="input-group mb-3">
                                                                      <span className="input-group-text bg-dark_ text-white">city:</span>
                                                                      <input
                                                                          name="city"
                                                                          value={address.city}
                                                                          onChange={updateInputAddress}
                                                                          type="text" className="form-control"/>
                                                                  </div>
                                                                  <div className="input-group mb-3">
                                                                      <span className="input-group-text bg-dark_ text-white">state:</span>
                                                                      <input
                                                                          name="state"
                                                                          value={address.state}
                                                                          onChange={updateInputAddress}
                                                                          type="text" className="form-control"/>
                                                                  </div>
                                                                  <div className="input-group mb-3">
                                                                      <span className="input-group-text bg-dark_ text-white">Country:</span>
                                                                      <input
                                                                          name="country"
                                                                          value={address.country}
                                                                          onChange={updateInputAddress}
                                                                          type="text" className="form-control"/>
                                                                  </div>
                                                                  <div className="input-group mb-3">
                                                                      <span className="input-group-text bg-dark_ text-white">Pin code:</span>
                                                                      <input
                                                                          name="pin"
                                                                          value={address.pin}
                                                                          onChange={updateInputAddress}
                                                                          type="text" className="form-control"/>
                                                                  </div>
                                                                  <div className="input-group mb-3">
                                                                      <span className="input-group-text bg-dark_ text-white">Mobile:</span>
                                                                      <input
                                                                          name="mobile"
                                                                          value={address.mobile}
                                                                          onChange={updateInputAddress}
                                                                          type="text" className="form-control"/>
                                                                  </div>
                                                                  <div className="text-center">
                                                                      <input  type="submit" className="btn btn-primary " value="Update"/>
                                                                  </div>
                                                              </form>
                                                          </div>
                                                      </div>
                                              }
                                          </div>
                                      </div>
                                  </div>
                              </div>
                          </section>
                      }
                  </React.Fragment>
          }
      </React.Fragment>
  )
};
export  default UserProfile;