import * as userAction from './user.action';

//features key
export const userFeatureKey = 'users';

//initialState
let initialState = {
    user:{},
    token:'',
    isAuthenticated: false,
    loading:false,
    errorMessage : '',
};

export const reducer = (state = initialState , action)=>{

    let {type , payload} = action;
    switch (type){
        case userAction.REGISTER_USER_REQUEST:
            return{
                ...state,
                loading:true,
            };
        case userAction.REGISTER_USER_SUCCESS:
            return{
                ...state,
                loading:false,
            };
        case userAction.REGISTER_USER_FAILURE:
            return{
                ...state,
                loading:false,
                errorMessage:payload
            };

            // login
        case userAction.LOGIN_USER_REQUEST:
            return{
                ...state,
                loading:true,
            };
        case userAction.LOGIN_USER_SUCCESS:
            localStorage.setItem(process.env.REACT_APP_LOGIN_TOKEN, payload.token);
            return{
                ...state,
                loading:false,
                token : payload.token,
                isAuthenticated: true
            };
        case userAction.LOGIN_USER_FAILURE:
            localStorage.removeItem(process.env.REACT_APP_LOGIN_TOKEN);
            return{
                ...state,
                loading:false,
                token :'',
                errorMessage:payload,
                isAuthenticated: false
            };
            //get userInfo
        case userAction.GET_USER_INFO_REQUEST:
            return{
                ...state,
                loading:true,
            };
        case userAction.GET_USER_INFO_SUCCESS:
            return{
                ...state,
                loading:false,
                user:payload.user
            };
        case userAction.GET_USER_INFO_FAILURE:
            return {
                ...state,
                loading: false,
                user: {},
                errorMessage: payload
            };
        // update Address
        case userAction.UPDATE_ADDRESS_REQUEST:
            return {
                ...state,
                loading: true
            };
        case userAction.UPDATE_ADDRESS_SUCCESS:
            return {
                ...state,
                loading: false,
                user: payload.user
            };
        case userAction.UPDATE_ADDRESS_FAILURE:
            return {
                ...state,
                loading: false,
                errorMessage: payload
            };
            //logout user
        case userAction.LOGOUT_USER:
            localStorage.removeItem(process.env.REACT_APP_LOGIN_TOKEN);
            return {
                ...state,
                loading: false,
                token:'',
                isAuthenticated:false,
            };


        default : return state;
    }

};
