import React from 'react';
import {useDispatch , useSelector} from 'react-redux';
import  {Link , useHistory} from 'react-router-dom';
import * as orderReducer from "../../redux/orders/order.reducer";
import * as userReducer from '../../redux/users/user.reducer';
import * as orderActions from '../../redux/orders/order.action';
import Spinner from '../../layout/util/Spinner/Spinner';
import  StripeCheckout from 'react-stripe-checkout';
import brand from '../../assets/img/brand.PNG';


let  CheckOut = ()=>{

    let history = useHistory();
    let dispatch = useDispatch();

    let productInfo = useSelector((state)=>{
        return state[orderReducer.OrderFearturesKey]
    });
    let {cartItems } =productInfo;

    let userInfo = useSelector((state)=>{
        return state[userReducer.userFeatureKey]
    });
    let {loading , user} = userInfo;

    let calcTotal = ()=>{
        let total = 0;
        for(let cartItem of cartItems){
            total += cartItem.price * cartItem.qty;
        }
        return total;
    };
    let calttax = ()=>{
        return calcTotal()* Number(process.env.REACT_APP_TAX) /100 ;

    };
    let grandTotal = ()=>{
        return calcTotal() + calttax();
    };

    let clickPayment = (token) => {
        let items = cartItems.map(item => {
            return {
                name : item.name,
                brand : item.brand,
                price : item.price,
                qty : item.qty
            }
        });
        let order = {
            items : items,
            tax : calttax(),
            total : calcTotal()
        };

        let product = {
            price : Number(grandTotal()) * 100,
            name : 'Products from  BrainsKart'
        };
        let body = {
            token , product
        };
        dispatch(orderActions.makeStripePayment(body , history , order));
    };


    return(
      <React.Fragment>
          <section className="bg-brown text-white p-2">
              <div className="container">
                  <div className="row">
                      <div className="col">
                          <h3><i className="fa fa-check-circle"/>checkout Your Items</h3>
                      </div>
                  </div>
              </div>
          </section>
          {
              loading ? <Spinner/> :

                  <React.Fragment>
                      {
                          Object.keys(user).length > 0 &&

                          <section className="mb-5">
                              <div className="container">
                                  <div className="row mt-5">
                                      <div className="col-md-8">
                                          <div className="row">
                                              <div className="col">
                                                  <div className="card">
                                                      <div className="card-header bg-dark_ text-white">
                                                          <p className="h4">
                                                              Billing Address
                                                              <Link to="/users/profile" className="btn btn-brown btn-sm text-dark float-right">Update Address</Link>
                                                          </p>

                                                      </div>
                                                      <div className="card-body">
                                                          <ul className="list-group">
                                                              <li className="list-group-item bg-brown">
                                                                  <small>MOBILE NO : {user.address.mobile}</small><br/>
                                                                  <small>HNO : {user.address.flat}</small><br/>
                                                                  <small> STREET : {user.address.street}</small><br/>
                                                                  <small>LANDMARK : {user.address.landmark}</small><br/>
                                                                  <small>CITY : {user.address.city}</small><br/>
                                                                  <small>State :{user.address.state} </small><br/>
                                                                  <small>COUNTRY : {user.address.country}</small><br/>
                                                                  <small>PIN CODE : {user.address.pin}</small><br/>
                                                                  <small>State :{user.address.state} </small><br/>
                                                              </li>
                                                          </ul>
                                                      </div>
                                                  </div>
                                              </div>
                                          </div>
                                          <div className="row mt-3">
                                              <div className="col">
                                                  <div className="card">
                                                      <div className="card-header bg-dark_ text-white">
                                                          <h3>Payment Details</h3>
                                                      </div>
                                                      <div className="card-body">
                                                          <div>
                                                              <form>
                                                                  <div className="form-group">
                                                                      <div className="form-check">
                                                                          <input className="form-check-input" type="radio" name="flexRadioDefault" />
                                                                          <label className="form-check-label" htmlFor="flexRadioDefault1">Cash on Delivery</label>
                                                                      </div>
                                                                      <div className="form-check">
                                                                          <input className="form-check-input" type="radio" name="flexRadioDefault" id="flexRadioDefault2" />
                                                                          <label className="form-check-label" htmlFor="flexRadioDefault2">Credit Card Payment</label>
                                                                      </div>
                                                                  </div>
                                                              </form>
                                                          </div>
                                                      </div>
                                                  </div>
                                              </div>
                                          </div>
                                      </div>
                                      <div className="col-md-4">
                                          <div className="card">
                                              <div className="card-header bg-dark_ text-white">
                                                  <h3>Your Cart</h3>
                                              </div>
                                              <div className="card-body">
                                                  {
                                                      cartItems.length > 0 ?
                                                          <React.Fragment>
                                                              <ul className="list-group">
                                                                  {
                                                                      cartItems.map(item =>{
                                                                          return(
                                                                              <li  key={item._id} className="list-group-item">
                                                                                  <div className="row">
                                                                                      <div className="col-md-3">
                                                                                          <img src={item.image} alt="" width="40" height="60"/>
                                                                                      </div>
                                                                                      <div className="col-md-9">
                                                                                          <h5>{item.name}</h5>
                                                                                          <small className="font-weight-bold">Price : &#8377; {item.price.toFixed(2)}</small><br/>
                                                                                          <small className="font-weight-bold">Qty : {item.qty}</small>
                                                                                      </div>
                                                                                  </div>
                                                                              </li>
                                                                          )

                                                                      })
                                                                  }
                                                              </ul>
                                                          </React.Fragment> :

                                                          <React.Fragment>
                                                                <p className="lead">Cart Empty.</p>
                                                          </React.Fragment>
                                                  }
                                              </div>
                                          </div>
                                          <div className="card mt-3">
                                              <div className="card-header">
                                                  <h4>payment</h4>
                                              </div>
                                              <div className="card-body">
                                                  <ul className="list-group">
                                                      <li className="list-group-item bg-brown">
                                                          Total : &#8377;{calcTotal().toFixed(2)}
                                                      </li>
                                                      <li className="list-group-item bg-brown">
                                                          Tax : &#8377;{calttax().toFixed(2)}
                                                      </li>
                                                      <li className="list-group-item bg-brown">
                                                          Grand Total : &#8377;{grandTotal().toFixed(2)}
                                                      </li>
                                                  </ul>
                                                  <StripeCheckout token={clickPayment}
                                                                  stripeKey={process.env.REACT_APP_PUBLIC_SECRET_KEY}
                                                                  name="Stripe Payment"
                                                                  amount={grandTotal() * 100}
                                                                  description="Payments with Stripe"
                                                                  currency="INR"
                                                                  image={brand}>
                                                      <button className="btn btn-danger btn-sm btn-block mt-3">PAY &#8377; {grandTotal()}</button>
                                                  </StripeCheckout>
                                              </div>
                                          </div>
                                      </div>
                                  </div>
                              </div>
                          </section>
                      }
                  </React.Fragment>
          }
      </React.Fragment>
  )
};
export  default CheckOut;