import React,{useEffect} from 'react';
import {useDispatch , useSelector} from 'react-redux';
import * as productReducer from "../../redux/products/product.reducer";
import * as productAction from "../../redux/products/product.action";
import Spinner from "../../layout/util/Spinner/Spinner";
import {Link , useHistory} from 'react-router-dom';
import * as orderAction from "../../redux/orders/order.action";

let  KidsWear = ()=>{

    let dispatch = useDispatch();
    let history = useHistory();

    useEffect(()=>{
        dispatch(productAction.getKidsProducts());
    },[]);

    let kidsProductInfo = useSelector((state)=>{
        return state[productReducer.productFeaturesKey]
    });
    let {loading  , products } = kidsProductInfo;

    let clickAddtoCart = (product)=>{
        product.qty=1;
        dispatch(orderAction.addToCart(product , history));
    };

    return(
      <React.Fragment>
          <section className="bg-brown text-white p-2">
              <div className="container">
                  <div className="row">
                      <div className="col">
                          <h3>Kid's Wear</h3>
                      </div>
                  </div>
              </div>
          </section>
          {
              loading ? <Spinner/> :
                  <React.Fragment>
                      <section>
                          <div className="container">
                              <div className="row">
                                  {
                                      products.map(product =>{
                                          return(
                                              <div className="col-md-3" key={product._id}>
                                                  <div className="card mt-3">
                                                      <div className="card-header bg-white">
                                                          <Link to={`/products/${product._id}`}>
                                                              <img src={product.image} alt="" className="img-fluid"/>
                                                          </Link>
                                                      </div>
                                                      <div className="card-body text-center">
                                                          <small className="lead font-weight-bold">{product.name}</small><br/>
                                                          <small className="lead font-weight-bold">{product.brand}</small><br/>
                                                          <small className="lead">&#8377; {product.price}</small><br/>
                                                          <button onClick={clickAddtoCart.bind(this , product)} className=" btn btn-brown btn-sm text-dark">Add to cart</button>

                                                      </div>
                                                  </div>
                                              </div>
                                          )
                                      })
                                  }
                              </div>
                          </div>
                      </section>
                  </React.Fragment>

          }
      </React.Fragment>
  )
};
export  default KidsWear;