import * as alertAction from './alert.action';
// features key
export  const alertFeaturesKey = 'alerts';


//initial state
let initialState = {
    alertMessages:[]
};

//reducer function
export  const reducer = (state = initialState , action)=>{

    let {type , payload}= action;
    switch (type){
        case alertAction.SET_ALERT:
            return {
                ...state ,
                alertMessages:[...state.alertMessages , payload]
            };
        case alertAction.REMOVE_ALERT:
            return {
                ...state,
                alertMessages: state.alertMessages.filter(alertMessage => alertMessage.id !== payload.id)
            };
        default:return state;
    }
};