import React from 'react';
import {Link} from 'react-router-dom';
import {useDispatch , useSelector} from 'react-redux';
import brand from '../../../assets/img/shopping-now.png';
import * as userActions from '../../../redux/users/user.action';
import * as userReducer from '../../../redux/users/user.reducer';
import * as userUtil from '../../util/userUtil';
import * as  orderReducer  from './../../../redux/orders/order.reducer';


let  Navbar = ()=>{

    let dispatch =useDispatch();
    let userInfo = useSelector((state)=>{
        return state[userReducer.userFeatureKey]
    });

    let { user} =userInfo;


    let logoutUser=()=>{
        dispatch(userActions.logoutUSer());
    };

    let cartItemInfo = useSelector((state)=>{
        return state[orderReducer.OrderFearturesKey]
    });
    let {cartItems} = cartItemInfo;
    
    let beforeLogin = (
        <React.Fragment>
            <li className="nav-item">
                <Link to="/users/login" className="nav-link">Login</Link>
            </li>
            <li className="nav-item">
                <Link to="/users/register" className="nav-link">Register</Link>
            </li>
        </React.Fragment>
    );

    let afterLogin = (
        <React.Fragment>
            {
                user &&
                <li className="nav-item">
                    <Link to="/users/profile" className="nav-link">
                        <img src={user.avatar} alt="" width="25" height="25" className="rounded-circle mr-2" />{user.name}
                    </Link>
                </li>
            }
            <li className="nav-item">
                <Link to="/" className="nav-link" onClick={logoutUser}>Logout</Link>
            </li>
        </React.Fragment>
    );
  return(
      <React.Fragment>
          <nav className="navbar navbar-expand-sm navbar-dark bg-dark_">
              <div className="container">
                  <Link to="/" className="navbar-brand">
                      <img src={brand} alt="" height="45" width="100"/>
                  </Link>
                  <div className="collapse navbar-collapse">
                      <ul className="navbar-nav">
                          <li className="nav-item">
                              <Link to="/products/men" className="nav-link">Men's Wear</Link>
                          </li>
                          <li className="nav-item">
                              <Link to="/products/kids" className="nav-link">Kids's Wear</Link>
                          </li>
                          <li className="nav-item">
                              <Link to="/products/women" className="nav-link">Women's Wear</Link>
                          </li>
                          <li className="nav-item">
                              <Link to="/products/upload" className="nav-link">Upload</Link>
                          </li>
                          <li className="nav-item">
                              <Link to="/orders/cart" className="nav-link">
                                  <i className="fa fa-shopping-cart"/>
                                  <span className="badge badge-secondary">{cartItems.length}</span>
                              </Link>
                          </li>
                          <li className="nav-item">
                              <Link to="/orders/list" className="nav-link">List</Link>
                          </li>
                      </ul>

                      <ul className="navbar-nav ml-auto">
                          {
                              userUtil.isLoggedIn()? afterLogin : beforeLogin
                          }
                      </ul>
                  </div>
              </div>
          </nav>
      </React.Fragment>
  )
};
export  default Navbar;