
import * as productActions from './product.action';

//features key
export  const productFeaturesKey = 'products';

//initial state
let initialState = {
    loading:false,
    errorMessage:'',
    products:[],
    selectedProduct:{},
    isAuthenticated:false
};

//reducer function
export  const reducer = (state = initialState , action)=>{

    let {type , payload}=action;

    switch (type){

        //upload products
        case productActions.UPLOAD_PRODUCT_REQUEST:
            return{
                ...state,
                loading:true
            };
        case productActions.UPLOAD_PRODUCT_SUCCESS:
            return{
                ...state,
                loading:false,

            };
        case productActions.UPLOAD_PRODUCT_FAILURE:
            return{
                ...state,
                loading:false,
                errorMessage:payload
            };

            //Get men a Product
        case productActions.GET_MEN_PRODUCT_REQUEST:
            return{
                ...state,
                loading:true
            };

        case productActions.GET_MEN_PRODUCT_SUCCESS:
            return{
                ...state,
                loading:false,
                products:payload.products
            };

        case productActions.GET_MEN_PRODUCT_FAILURE:
            return{
                ...state,
                loading:false,
                errorMessage:payload.error
            };
        //Get women a Product
        case productActions.GET_WOMEN_PRODUCT_REQUEST:
            return{
                ...state,
                loading:true
            };

        case productActions.GET_WOMEN_PRODUCT_SUCCESS:
            return{
                ...state,
                loading:false,
                products:payload.products
            };
        case productActions.GET_WOMEN_PRODUCT_FAILURE:
            return{
                ...state,
                loading:false,
                errorMessage:payload.error
            };

        //Get kids  Product
        case productActions.GET_KIDS_PRODUCT_REQUEST:
            return{
                ...state,
                loading:true
            };
        case productActions.GET_KIDS_PRODUCT_SUCCESS:
            return{
                ...state,
                loading:false,
                products:payload.products
            };
        case productActions.GET_KIDS_PRODUCT_FAILURE:
            return{
                ...state,
                loading:false,
                errorMessage:payload.error
            };
        case productActions.GET_SINGLE_PRODUCT_REQUEST:
            return{
                ...state,
                loading:true
            };
        case productActions.GET_SINGLE_PRODUCT_SUCCESS:
            return{
                ...state,
                loading:false,
                selectedProduct:payload.products
            };
        case productActions.GET_SINGLE_PRODUCT_FAILURE:
            return{
                ...state,
                loading:false,
                errorMessage:payload.error
            };
        default: return state;
    }
};