import React from 'react';
import {useSelector} from 'react-redux';
import {Link} from 'react-router-dom';
import * as orderReducer from '../../redux/orders/order.reducer';
import Spinner from '../../layout/util/Spinner/Spinner';



let  OrderSuccess = ()=>{



    let orderInfo = useSelector((state) => {
        return state[orderReducer.OrderFearturesKey];
    });

    let { placedOrder , loading } = orderInfo;


    // calc Grand Total
    let calculateGrandTotal = () => {
        let total = 0;
        if(placedOrder.items?.length > 0){
            for(let item of placedOrder.items){
                total += Number(item.qty) * Number(item.price);
            }
        }
        return (total + Number(placedOrder.tax));
    };

    let takePrint = (e) => {
        e.preventDefault();
        return window.print();
    };

  return(
      <React.Fragment>
          <section className="bg-brown text-white p-2">
              <div className="container">
                  <div className="row">
                      <div className="col">
                          <h3><i className="fa fa-check-circle"/>Order Summery</h3>
                      </div>
                  </div>
              </div>
          </section>
          {
              loading ? <Spinner/> :
                  <React.Fragment>
                      <section className="mt-3 p-3">
                          {
                              Object.keys(placedOrder).length > 0 ? <React.Fragment>
                                  <div className="container">
                                      <div className="row">
                                          <div className="col-md-10 m-auto">
                                              <div className="card">
                                                  <div className="card-header bg-info text-white">
                                                      <p className="h3">Order Summary</p>
                                                  </div>
                                                  <div className="card-body">
                                                      <ul className="list-group">
                                                          <li className="list-group-item">
                                                              Order ID : {placedOrder._id}
                                                          </li>
                                                          <li className="list-group-item">
                                                              NAME : {placedOrder.name}
                                                          </li>
                                                          <li className="list-group-item">
                                                              Email : {placedOrder.email}
                                                          </li>
                                                          <li className="list-group-item">
                                                              Mobile : {placedOrder.mobile}
                                                          </li>
                                                          <li className="list-group-item">
                                                              DATE : {new Date(placedOrder.createdAt).toLocaleDateString()}
                                                          </li>
                                                      </ul>
                                                      <table className="table table-hover text-left table-striped table-light">
                                                          <thead>
                                                          <tr>
                                                              <th>SNO</th>
                                                              <th>Item Name</th>
                                                              <th>Brand</th>
                                                              <th>Item Qty</th>
                                                              <th>Item Price</th>
                                                              <th>Total</th>
                                                          </tr>
                                                          </thead>
                                                          <tbody>
                                                          {
                                                              placedOrder.items?.length > 0 ? <React.Fragment>
                                                                  {
                                                                      placedOrder.items?.map((item, index) => {
                                                                          return (
                                                                              <tr key={item._id}>
                                                                                  <td>{index+1}</td>
                                                                                  <td>{item.name}</td>
                                                                                  <td>{item.brand}</td>
                                                                                  <td>{item.qty}</td>
                                                                                  <td>&#8377;{item.price}</td>
                                                                                  <td>&#8377;{(item.price * item.qty).toFixed(2)}</td>
                                                                              </tr>
                                                                          )
                                                                      })
                                                                  }
                                                              </React.Fragment> : null
                                                          }
                                                          {
                                                              Object.keys(placedOrder).length > 0 &&
                                                              <tr>
                                                                  <td colSpan="4"/>
                                                                  <td>Tax : &#8377;
                                                                      <span className="text-danger font-weight-bold">{Number(placedOrder.tax)?.toFixed(2)}</span>
                                                                  </td>
                                                              </tr>
                                                          }
                                                          <tr>
                                                              <td colSpan="4"/>
                                                              <td>Grand Total : &#8377;
                                                                  <span className="text-primary">{calculateGrandTotal().toFixed(2)}</span>
                                                              </td>
                                                          </tr>
                                                          </tbody>
                                                      </table>
                                                      <p className="h5">NOTE : Your shipment will be delivered within 3 business days</p>
                                                      <button onClick={takePrint} className="btn btn-info btn-sm">Print Doc</button>
                                                      <Link to="/" className="btn btn-brown text-dark btn-sm">Done</Link>
                                                  </div>
                                              </div>
                                          </div>
                                      </div>
                                  </div>
                              </React.Fragment> : <React.Fragment>
                                  <p className="h3">Please select some items</p>
                              </React.Fragment>
                          }
                      </section>
                  </React.Fragment>
          }


      </React.Fragment>
  )
};
export  default OrderSuccess;