import {combineReducers} from 'redux';
import * as userReducer from '../redux/users/user.reducer';
import * as alertReducer from '../redux/alert/alert.reducer';
import * as productReducer from '../redux/products/product.reducer';
import * as orderReducer from '../redux/orders/order.reducer';

 export const rootReducer =  combineReducers({
    [userReducer.userFeatureKey]:userReducer.reducer,
     [alertReducer.alertFeaturesKey]:alertReducer.reducer,
     [productReducer.productFeaturesKey]:productReducer.reducer,
     [orderReducer.OrderFearturesKey]:orderReducer.reducer

});