import React,{useEffect} from 'react';
import {BrowserRouter as Router , Route , Switch} from 'react-router-dom';
import './App.css';
import Navbar from "./layout/component/navabr/Navbar";
import Home from "./layout/component/home/Home";
import MensWear from "./modules/product/MensWear";
import KidsWear from "./modules/product/KidsWear";
import WomenWear from "./modules/product/WomenWear";
import UploadProduct from "./modules/product/UploadProduct";
import UserProfile from "./modules/user/UserProfile";
import UserLogin from "./modules/user/UserLogin";
import UserRegister from "./modules/user/UserRegister";
import Cart from "./modules/order/Cart";
import Alert from '../src/layout/util/alert/Alert';
import {useDispatch , useSelector} from 'react-redux';
import * as userActions from './redux/users/user.action';
import ProductDetails from "./modules/product/ProductDetails";
import CheckOut from "./modules/order/CheckOut";
import OrderSuccess from "./modules/order/OrderSuccess";
import OrderList from "./modules/order/OrderList";
import PrivateRoot from "./layout/util/privateRoot";



let  App = ()=> {

let dispatch = useDispatch();
useEffect(()=>{
    if (localStorage.getItem(process.env.REACT_APP_LOGIN_TOKEN)){
        dispatch(userActions.getUserInfo());
    }
},[]);
  return (
      <React.Fragment>
              <Router>
                  <Navbar/>
                  <Alert/>
                  <Switch>
                      <Route exact path='/' component={Home}/>
                      <Route exact path='/products/men' component={MensWear}/>
                      <Route exact path='/products/kids' component={KidsWear}/>
                      <Route exact path='/products/women' component={WomenWear}/>
                      <PrivateRoot exact path="/products/upload" component={UploadProduct}/>
                      <PrivateRoot exact path="/products/:productId" component={ProductDetails}/>
                      <Route exact path='/orders/cart' component={Cart}/>
                      <Route exact path='/orders/checkout' component={CheckOut}/>
                      <PrivateRoot exact path='/orders/order-success' component={OrderSuccess}/>
                      <PrivateRoot exact path='/orders/list' component={OrderList}/>
                      <PrivateRoot exact path='/users/profile' component={UserProfile}/>
                      <Route exact path='/users/login' component={UserLogin}/>
                      <Route exact path='/users/register' component={UserRegister}/>
                  </Switch>
              </Router>
      </React.Fragment>


  );
};

export default App;
